/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

nav {
  background-color: #2b994c;
  padding: 1rem 0;
}

nav h2 {
  display: inline;
  margin: 0 2vw;
  font-size: 1.25rem;
  color: white;
}

nav a {
  margin: 0 2vw;
  font-size: 1rem;
  color: white;
  text-decoration: none;
}
nav a:hover {
  background-color: #33658a;
  height: 100%;
}
nav a:visited {
  color: white;
}

header h1 {
  padding: 20px;
  font-size: 5vw;
  text-align: center;
  color: white;
  padding: 0;
}

header p {
  font-size: 2vw;
  text-align: center;
  color: white;
}

h3 {
  font-size: 1.5rem;
}

#searchContainer,
#resultsContainer {
  color: white;
  background-color: grey;
  padding: 2vw;
  margin: 2vw;
  font-size: 3vw;
}

form {
  padding: 1rem 0 1.5rem;
  position: relative;
}

label {
  font-size: 1.25rem;
}

input {
  margin: 1vw 0;
  width: 98%;
  padding: 0.75vw;
  font-size: 1.25vw;
}

button {
  border-radius: 0;
  border: none;
  background-color: rgb(211, 16, 16);
  color: white;
  padding: 0.75vw;
  font-size: 1.25vw;
  -webkit-align-content: center;
          align-content: center;
}

form button {
  position: absolute;
  right: 0;
}

button[disabled] {
  background-color: #f9e39f;
}

#resultsContainer {
  font-size: 1rem;
}

#resultsContainer h3 {
  margin-bottom: 3.5vw;
}

.bookResult {
  background-color: white;
  color: black;
  padding: 1.75vw;
  font-size: 1rem;
}

header {
  background-image: url("https://images.pexels.com/photos/159711/books-bookstore-book-reading-159711.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500");
}

body {
  background-color: black;
}

.result-button {
  text-align: right;
}

